
const header: Element | null = document.querySelector(".header");
const humburger: Element | null = document.querySelector(".humburger");
const headerItem: Element | null = document.querySelector(".header__item");



export const containHide = (elem: HTMLElement | null) => elem?.classList.contains("hide");

export const scrollPosition = () => {
    return  window.pageYOffset || document.documentElement.scrollTop;
}

export const openItemMenu = () => {
   headerItem?.classList.toggle("active");
   console.log(headerItem);

}
