import React from "react";
import { NavLink } from "react-router-dom";

export const MainInfo: React.FC = () => {
  return (
    <div className="start-box ">
      <div className="container">
        <div className="start-box__title">Смэп Гродненского Облисполкома</div>
        <div className="start-box__subtitle">
          «СМЭП Гродненского облисполкома» занимается изготовлением, установкой
          и обслуживанием технических средств регулирования дорожного движения,
          нанесением дорожной разметки эмалью.
        </div>
        <div className="start-box__wr-btn">
          <NavLink
            onClick={() => {
              document.body.scrollIntoView({
                block: "start",
              });
            }}
            className="btn-info"
            to="/Company"
          >
            узнать больше
          </NavLink>
          <NavLink
            onClick={() => {
              document.body.scrollIntoView({
                block: "start",
              });
            }}
            className="btn"
            to="/Contacts"
          >
            связаться
          </NavLink>
        </div>
      </div>
    </div>
  );
};
