import React from "react";
import { NavLink } from "react-router-dom";

export const Services: React.FC = () => (
  <div className="services">
    <div className="bg">
      <div className="container">
        <div id="serv" className="st-title pt-100">
          Наши услуги
        </div>
        <div className="st-subtitle">
          «СМЭП Гродненского облисполкома» занимается изготовлением, установкой
          и обслуживанием технических средств регулирования дорожного движения,
          нанесением дорожной разметки эмалью.
        </div>
        <div className="services__items">
          <div className="item">
            <div className="item__img i-1"></div>
            <div className="item__title">Изготовление</div>
          </div>
          <div className="item">
            <div className="item__img i-2"></div>
            <div className="item__title">Нанесение разметки</div>
          </div>
          <div className="item">
            <div className="another-bg"></div>
            <div className="item__img i-3"></div>
            <div className="item__title white">Установка знаков</div>
          </div>
          <div className="item">
            <div className="item__img i-4"></div>
            <div className="item__title">Ремонт</div>
          </div>
        </div>
        <div className="services__wrapper">
          <div className="descr">Узнайте больше о наших услугах</div>
          <NavLink
            onClick={() => {
              document.body.scrollIntoView({
                block: "start",
              });
            }}
            className="btn"
            to="/Services"
          >
            НАШИ УСЛУГИ
          </NavLink>
        </div>
      </div>
    </div>
  </div>
);
