import React, { useState } from "react";
import { AboutComp } from "../components/company/AboutComp";
import { AboutUs } from "../components/company/AboutUs";
import { Managers } from "../components/company/Managers";
import { Reviews } from "../components/company/Reviews";
import { Certificates } from "../components/company/Сertificates";
import { Footer } from "../components/footer/footer";
import { useInView } from "react-intersection-observer";
import { clsx } from "clsx";
import { InView } from "react-intersection-observer";

export const CompanyPage: React.FC = () => {
  const { ref, inView, entry } = useInView({
    /* Optional options */
    threshold: 0,
  });
  const [aboutUs, setAboutUs] = useState(false);
  const test = clsx("default", { inView: inView });
  return (
    <div className="container">
      <div className="company">
        <AboutComp />
        <InView as="div" onChange={(inView, entry) => setAboutUs(inView)}>
          <AboutUs isInView={aboutUs as boolean} />
        </InView>
        <Certificates />
        <Managers />
        <Reviews />
        {/* <div ref={ref}>
        <div className={test}>{`isInView ${inView}`}</div>
      </div> */}
        <Footer />
      </div>
    </div>
  );
};
