import React, { ReactElement } from "react";
import { clsx } from "clsx";
export const AboutUs = (props: { isInView: boolean }): ReactElement => {
  const containerClasses = clsx({
    "about-us": true,
    "test class": props.isInView,
  });
  return (
    <div className={containerClasses}>
      <div className="container">
        <div className="about-us-wrapper">
          <div className="about-us-descr">
            <div className="about-us-subtitle">О нас</div>
            <div className="about-us-title">Компания</div>
            <div className="about-us-title blue-t">СМЭП</div>
            <div className="about-us-text">
              Lorem Ipsum - это текст-"рыба", часто используемый в печати и
              вэб-дизайне. Lorem Ipsum является стандартной "рыбой" для текстов
              на латинице с начала XVI века. В то время некий безымянный
              печатник создал большую коллекцию размеров и форм шрифтов,
              используя Lorem Ipsum для распечатки образцов. Lorem Ipsum не
              только успешно пережил без заметных изменений пять веков, но и
              перешагнул в электронный дизайн.
            </div>
          </div>
          <div className="about-us-items">
            <div className="about-us-item">
              <div className="about-us-item-number">90</div>
              <div className="about-us-item-descr">
                Завершено крупных сделок
              </div>
            </div>
            <div className="about-us-item">
              <div className="about-us-item-number">90</div>
              <div className="about-us-item-descr">
                Завершено крупных сделок
              </div>
            </div>
            <div className="about-us-item">
              <div className="about-us-item-number">90</div>
              <div className="about-us-item-descr">
                Завершено крупных сделок
              </div>
            </div>
            <div className="about-us-item blue-b">
              <div className="about-us-item-number blue-b">90</div>
              <div className="about-us-item-descr blue-b">
                Завершено крупных сделок
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
