import React from "react";
import "react-slideshow-image/dist/styles.css";
import Carousel from "react-bootstrap/Carousel";
import roadImg1 from "../../img/slider/1.png";
import roadImg2 from "../../img/slider/2.jpeg";
import roadImg3 from "../../img/slider/3.jpg";

export const Slider: React.FC = () => {
  return (
    <div className="slider">
      <Carousel>
        <Carousel.Item>
          <img className="d-block  width-custom" src={roadImg1} alt="road" />
          <Carousel.Caption></Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img className="d-block  width-custom" src={roadImg2} alt="road" />
          <Carousel.Caption></Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img className="d-block  width-custom" src={roadImg3} alt="road" />
          <Carousel.Caption></Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    </div>
  );
};
