import React from "react";
import { NavLink, useNavigate } from "react-router-dom";

export const Vacancy: React.FC = () => {
  const navigate = useNavigate();
  return (
    <div className="bg">
      <div className="container">
        <div className="path-tree">
          <div className="path-tree-btn" onClick={() => navigate("/")}>
            СМЭП Гродненской области {`>`}
          </div>
          <div
            className="path-tree-btn"
            onClick={() => navigate("/CompanyPage")}
          >
            Вакансии
          </div>
        </div>
        <div className="comp-title">Вакансии</div>
        <div className="vacancy">
          <div className="vacancy-text">
            Список открытых вакансий в текущее время. Вакансии на 2023 год:
          </div>
          <div className="vacancy-items">
            <div className="vacancy-item">
              <div className="vacancy-img vac-img-1"></div>
              <div className="vacancy-title">Инженер-конструктор</div>
              <div className="vacancy-descr">
                Установка дорожных знаков; нанесение линий пешеходного перехода
                вручную при помощи шаблона;
              </div>
            </div>
            <div className="vacancy-item">
              <div className="vacancy-img vac-img-2"></div>
              <div className="vacancy-title">Инженер-инспектор</div>
              <div className="vacancy-descr">
                Установка дорожных знаков; нанесение линий пешеходного перехода
                вручную при помощи шаблона;
              </div>
            </div>
            <div className="vacancy-item">
              <div className="vacancy-img vac-img-3"></div>
              <div className="vacancy-title">Инженер-техник</div>
              <div className="vacancy-descr">
                Установка дорожных знаков; нанесение линий пешеходного перехода
                вручную при помощи шаблона;
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="vacancy-contact">
        <div className="vacancy-contact-title">
          Бесплатно проконсультируем по всем интересующим вопросам
        </div>
        <div className="vacancy-contact-descr">
          <div className="vacancy-contact-subtitle">
            Появились вопросы? С радостью ответим.
          </div>
          <NavLink
            onClick={() => {
              document.body.scrollIntoView({
                block: "start",
              });
            }}
            className="vacancy-contact-btn"
            to="/Services"
          >
            Контакты
          </NavLink>
        </div>
      </div>
      <div className="services__wrapper pt-50">
        <div className="descr">Узнайте больше о наших услугах</div>
        <NavLink
          onClick={() => {
            document.body.scrollIntoView({
              block: "start",
            });
          }}
          className="btn"
          to="/Services"
        >
          НАШИ УСЛУГИ
        </NavLink>
      </div>
    </div>
  );
};
