/* eslint-disable jsx-a11y/iframe-has-title */
import React, { useState } from "react";

export const Map: React.FC = () => {
  const [map, setmap] = useState(true);
  return (
    <div className="map">
      <div
        className="map-wrapper"
        onClick={() => setmap((prev) => !prev)}
        onMouseLeave={() => setmap(true)}
      >
        {map && (
          <div className="map-locker">
            <div className="map-locker-text">
              Нажмите чтобы взаимодействовать
            </div>
          </div>
        )}
        <iframe
          src="https://yandex.ru/map-widget/v1/?um=constructor%3A3223ab1b36a7f05abac9f8f5770e83f9755b2d6237f87dd562177052f8ad014e&amp;source=constructor"
          width="100%"
          height="100%"
          frameBorder="0"
        ></iframe>
      </div>
      <div className="container">
        <div className="cont-pop-up">
          <div className="pop-up-wrapper">
            <div className="pop-up-title">
              Появились вопросы? С радостью ответим.
            </div>
            <div className="pop-up-contacts">
              <div className="contact-item">Name contact - 375(...) 123</div>
              <div className="contact-item">Name contact - 375(...) 123</div>
              <div className="contact-item">Name contact - 375(...) 123</div>
              <div className="contact-item">Name contact - 375(...) 123</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
